@tailwind base;
@tailwind components;
@tailwind utilities;


*{
  margin: 0px;
  padding: 0;
  box-sizing: border-box;
}


.forms{
  border: 1px solid red;
  padding: 20px;
  position: absolute;
  top: 25%;
  left: 40%;
  transform: translate(-50% -50%);
}

.forms div{
  width: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px 0;
}

.forms input{
  border: 1px solid black;
}

.forms button{
  padding: 0 10px;
  background-color: red;
  cursor: pointer;
}
