:root {
    --dullWhite: rgb(205, 202, 202);
}

nav {
    width: 100%;
    background-color: rgb(72, 50, 72);
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}

.left-nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* width: 25%; */
    font-size: 18px;
    margin-right: 20px;
}

.left-nav button {
    padding: 5px 15px;
    background-color: var(--dullWhite);
    border-radius: 10px;
    color: black;
    margin: 0px 8px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.mid-nav {
    position: relative;
}

.mid-nav input {
    width: 400px;
    height: 40px;
    border-radius: 6px;
    padding: 0px 10px;
    position: relative;
    border: 1px solid black;
}

.mid-nav input:focus {
    outline: none;
}

.search-icon {
    position: absolute;
    right: 5px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 26px;
}


.ham-burger {
    color: white;
    font-size: 40px;
}

.related-search-bar{
    position: absolute;
    padding: 10px 10px;
    width: 400px;
    border-radius: 5px;
    background-color: aliceblue;
    z-index: 1;
}
.related-search-bar p {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 10px;
    font-weight: 600;
    
}
.related-search-box:hover{
    background-color: white;
    color: black;
}
.related-search-box{
    display: flex;
    border: 1px solid white;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    color: white;
    margin-bottom: 2px;
    padding: 5px;
    border-radius: 5px;
    transition: all 0.2s ease;
}

.related-search-box img{
    border-radius: 50%;
    background-color: aliceblue;
}
