nav {
    display: flex;
    justify-content: center;
    align-items: center;
}

nav input {
    width: 400px;
    height: 40px;
    border-radius: 6px;
    padding: 0px 10px;
    position: relative;
    border: 1px solid black;
}

nav input:focus {
    outline: none;
}

.search-icon {
    position: absolute;
    right: 5px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 26px;
}






