.ham-burger {
    color: white;
    font-size: 40px;
}

.related-search-bar{
    position: absolute;
    padding: 10px 10px;
    width: 400px;
    border-radius: 5px;
    background-color: aliceblue;
    z-index: 1;
    top: 55px;
}
.related-search-bar p {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 10px;
    font-weight: 600;
    
}
.related-search-box:hover{
    background-color: white;
    color: black;
}
.related-search-box{
    display: flex;
    border: 1px solid white;
    justify-content: space-between;
    align-items: center;
    background-color: black;
    color: white;
    margin-bottom: 2px;
    padding: 5px;
    border-radius: 5px;
    transition: all 0.2s ease;
}

.related-search-box img{
    border-radius: 50%;
    background-color: aliceblue;
}
