.box{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    /* border-bottom: 2px solid black; */
    width: 100%;
    height: 100%;
    align-items: center;
    padding: 20px 0;
}

.box img{
    height: 100px;
    width: 100px;
    border-radius: 50%;
    border: 1px solid black;
}

.box h1{
    background-color: black;
    color: white;
    font-size: larger;
    font-weight: 600;
    padding: 0 10px;
    margin-bottom: 5px;
}