.parent-container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 100px 50px;
    position: relative;
    margin-bottom: 50px;
}

.container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    row-gap: 20px;
    column-gap: 20px;
    width: 80%;
}

.card-parent {
    position: relative;
}


.card {
    background-color: white;
    display: flex;
    flex-direction: column;
    column-gap: 30px;
    row-gap: 30px;
    height: 250px;
    border: 1px solid white;
    align-items: center;
    justify-content: space-around;
    font-weight: 600;
    border-radius: 5px;
    border: 1px solid black;
    transition: 0.1s ease-in;
}

.card:hover {
    transform: translateY(-10px);
    box-shadow: 5px 1px 7px 2px rgb(192, 188, 188);
}



.available {
    background-color: #bff47b;
}

.card img {
    width: 150px;
    height: 150px;
}


.sidebar {
    width: 400px;
    height: 50vh;
    margin-left: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    border-radius: 5px;
    overflow: hidden;
    border: 2px solid black;
    position: sticky;
    top: 20px;
    box-shadow: 5px 2px 13px 2px rgb(179, 174, 174);

}


.upper-sidebar {
    display: flex;
    width: 100%;
    height: 50%;
    align-items: center;
    justify-content: center;

}

.filter-bar {
    display: flex;
    flex-direction: column;
    box-shadow: 1px 1px 10px 1px gray;
    padding: 10px 0;
}

.filter-bar .row {
    margin: 5px 5px;

}

.filter-bar .row input {
    background-color: rgb(235, 233, 233);
    padding: 5px;
}

.filter-bar .t-row {
    display: flex;
    flex-direction: column;
}

.filter-bar .t-row .pagination {
    position: absolute;
    bottom: 0;
    /* width: 300px; */
    /* background-color: #bff47b; */
    border: 1px solid black;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    left: 50%;
    transform: translateX(-50%);
}

.pagination input {
    border: 1px solid black;
}

.pagination input:focus{
    outline: none;
}

.pagination div {
    display: flex;
    /* width: 150px; */
    justify-content: center;
    align-items: center;
    /* color: white; */
    /* border: 1px solid black; */
}

.pagination button {
    color: red;
}

.filter-bar {
    display: flex;
    flex-direction: column;
    margin-right: 10px;
    position: sticky;
    top: 10px;
}