.team-container {
    width: 500px;
    height: 400;
    background-color: white;
    border: 1px solid black;
    border-radius: 5px;
    top: -50%;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    box-shadow: 1px 1px 10px 1px gray;
    transition: top 1s ease-in;
}
.lower-sidebar{
    position: relative;
}

.close-icon {
    position: absolute;
    top: -75px;
    right: -95px;
    font-size: 20px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.1s ease-in;
}

.close-icon:hover{
    background-color: rgb(221, 226, 230);
}



.team-container h1 {
    font-weight: 600;
    margin-bottom: 10px;
    font-size: 20px;
}

.active {
    top: 50%;
    transform: translate(-50%, -50%);
}



.team-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.team-container input {
    width: 220px;
    height: 30px;
    border: 1px solid black;
    padding: 0 10px;
}

.team-container button {
    width: 90%;
    background-color: #bff47b !important;
    height: 40px;
    border-radius: 3px;
    font-weight: 600;
}


.icon {
    position: absolute;
    border-radius: 50%;
    right: 0;
    top: 0;
    cursor: pointer;
    z-index: 1;
    font-size: 25px;
    color: #000;
}

.team-members {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 10px;
    margin: 10px 0;
    border: 2px solid black;
    padding: 20px;
    width: 300px;
    /* height: 200px; */

}

.team-member {
    display: flex;
    width: 80%;
    justify-content: space-between;
    align-items: center;
    padding: 5px 10px;
    border: 2px solid black;
    border-radius: 5px;

}

.team-member img {
    height: 30px;
    width: 40px;
    border-radius: 50%;
}