.right-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.right-nav button {
    margin: 0 10px;
    width: 80px;
    background-color: aliceblue;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
}

.right-nav button:hover {
    background-color: rgb(215, 221, 227);
}